/* Landing CSS */

.wrapper {
  width: 1170px;
  margin: 0 auto;
  @media #{$tablet} {
    width: 768px;
  }
  @media #{$mobile} {
    width: 300px;
    padding: 0 10px;
  }
}

.top_section {
  background: url(images/dist/topbg.png) no-repeat;
  padding: 35px 0 35px;
  .logo {
    display: block;
    margin-bottom: 25px;
    @media #{$mobile} {
      text-align: center;
    }
  }
  .top_blk_content {
    background: url(images/dist/top_content_block_bg.png);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
    height: 665px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    border-radius: 20px;
    box-shadow: 0px 15px 20px -1px rgba(20, 38, 120, 1);
    @media #{$tablet} {
      height: 435px;
    }
    @media #{$mobile} {
      background-image: url(images/dist/top_content_block_bg.png);
      background-color: #0c2571;
      background-position: center bottom;
      height: auto;
      padding: 25px 10px 150px;
    }
    .top_blk_text {
      width: 470px;
      margin-right: 50px;
      @media #{$tablet} {
        width: 280px;
        margin-right: 15px;
      }
      @media #{$mobile} {
        width: 100%;
        margin: 0;
      }
      img {
        margin: 0 0 10px;
        @media #{$tablet} {
          width: 175px;
        }
        @media #{$mobile} {
          display: none;
        }
      }
      .subtitle {
        color: #ffffff;
        font-family: $font__headers;
        font-weight: 700;
        text-transform: uppercase;
        font-size: 26px;
        line-height: 28px;
        margin: 0 0 50px;
        @media #{$tablet} {
          font-size: 18px;
          margin-bottom: 25px;
        }
        @media #{$mobile} {
          font-size: 18px;
          margin-bottom: 25px;
        }
      }
      .text {
        color: #a7fef4;
        font-size: 18px;
        line-height: 22px;
        font-weight: 400;
        margin: 0 0 20px;
        @media #{$tablet} {
          font-size: 16px;
        }
        @media #{$mobile} {
          font-size: 16px;
        }
      }
      .contact_us {
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #fdd011;
        border-radius: 5px;
        color: #febf00;
        font-size: 18px;
        font-weight: 700;
        font-family: Malgun, sans-serif;
        transition: all 0.3s;
        text-decoration: none;
        width: 160px;
        height: 43px;
        text-transform: uppercase;
        &:hover {
          background: #fdd011;
          border-color: #fdd011;
          color: #fff;
        }
      }
    }
  }
}

.cr_currency_logos_section {
  background: #cbced6;
  padding: 15px 0;
  .cr_currency_logos_wrap {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    img {
      margin: 0 60px;
      @media #{$tablet} {
        margin: 0 35px;
      }
      @media #{$mobile} {
        width: 40px;
        margin: 0 10px;
      }
    }
  }
}

.how_it_works_section {
  background: #051c3e;
  padding: 85px 0 75px;
  @media #{$mobile} {
    padding: 50px 0;
  }
  .section_title {
    color: #febf00;
    font-size: 26px;
    text-align: center;
    font-weight: 700;
    font-family: $font__headers;
    margin: 0 0 50px;
    text-transform: uppercase;
    @media #{$mobile} {
      font-size: 22px;
    }
  }
  .items_wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    .item {
      width: 370px;
      @media #{$tablet} {
        width: 235px;
      }
      @media #{$mobile} {
        width: 300px;
        margin: 0 auto 35px;
      }
      img {
        margin: 0 0 20px;
        @media #{$tablet} {
          width: 235px;
        }
        @media #{$mobile} {
          width: 300px;
        }
      }
      .item_step {
        width: 45px;
        height: 45px;
        margin: 0 auto 20px;
        border-radius: 50px;
        border: 2px solid #24b6de;
        display: flex;
        align-items: center;
        justify-content: center;
        color: #24b6de;
        font-size: 30px;
        position: relative;
      }
      .item_step:after {
        content: '';
        display: block;
        width: 355px;
        background: #24b6de;
        height: 2px;
        position: absolute;
        left: 45px;
        top: 21px;
        @media #{$tablet} {
          width: 220px;
        }
        @media #{$mobile} {
          display: none;
        }
      }
      .item_text {
        font-size: 20px;
        font-weight: 300;
        line-height: 22px;
        color: #fff;
        text-align: center;
        padding: 0 20px;
        @media #{$tablet} {
          font-size: 16px;
          padding: 0 10px;
        }
        @media #{$mobile} {
          font-size: 16px;
          padding: 0 10px;
        }
      }
      &:last-child {
        .item_step:after {
          display: none;
        }
      }
    }
  }
}

.gateway_section {
  background: url(images/dist/gateway_bg.jpg);
  min-height: 779px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  @media #{$tablet} {
    min-height: 500px;
  }
  @media #{$mobile} {
    background-position: center right;
  }
  .wrapper {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 779px;
    @media #{$tablet} {
      min-height: 500px;
      height: 500px;
    }
  }
  .gateway_text {
    width: 520px;
    @media #{$tablet} {
      width: 350px;
    }
    .title {
      font-weight: 600;
      font-size: 36px;
      color: #febf00;
      margin: 0 0 10px;
      @media #{$tablet} {
        font-size: 24px;
      }
    }
    .text {
      font-size: 20px;
      line-height: 26px;
      font-weight: 300;
      color: #000000;
      margin: 0 0 70px;
      @media #{$tablet} {
        font-size: 16px;
        line-height: 24px;
        margin: 0 0 30px;
      }
    }
    .underline {
      width: 100%;
      height: 10px;
      background: rgb(7,25,115); /* Old browsers */
      background: -moz-linear-gradient(left, rgba(7,25,115,1) 0%, rgba(11,128,185,1) 100%); /* FF3.6-15 */
      background: -webkit-linear-gradient(left, rgba(7,25,115,1) 0%,rgba(11,128,185,1) 100%); /* Chrome10-25,Safari5.1-6 */
      background: linear-gradient(to right, rgba(7,25,115,1) 0%,rgba(11,128,185,1) 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#071973', endColorstr='#0b80b9',GradientType=1 ); /* IE6-9 */
    }
  }
}

.contacts_form {
  background: url(images/dist/form_block_bg.jpg) no-repeat;
  padding: 40px 0 40px;
  background-position: center;
  background-size: cover;
  .contact_form_block {
    box-sizing: border-box;
    background: url(images/dist/form_bg.png) no-repeat;
    background-size: 100%;
    padding: 70px 25px 25px;
    margin: 0px 0 0;
    height: 483px;
    @media #{$tablet} {
      height: auto;
      background: #0c2571;
      border-radius: 10px;
    }
    @media #{$mobile} {
      height: auto;
      background: #0c2571;
      border-radius: 10px;
    }
    .title {
      font-size: 31px;
      line-height: 31px;
      margin: 0 0 15px;
      font-family: $font__headers;
      font-weight: 700;
      text-align: center;
      color: #fff;
      text-transform: uppercase;
      @media #{$mobile} {
        font-size: 22px;
        line-height: 26px;
      }
    }
    .subtitle {
      font-weight: 400;
      color: #febf00;
      font-size: 21px;
      text-align: center;
      margin: 0 0 50px;
      @media #{$mobile} {
        font-size: 18px;
      }
    }
    form p {
      display: flex;
      justify-content: space-between;
      align-items: center;
      flex-wrap: wrap;
      width: 100%;
    }
    br {
      display: none;
    }
    .wpcf7-form-control-wrap {
      margin-bottom: 30px;
    }
    input {
      box-sizing: border-box;
      display: block;
      background: none;
      border: none;
      border-bottom: 1px solid #fff;
      color: #fff;
      font-size: 20px;
      font-weight: 300;
      width: 500px;
      padding-bottom: 5px;
      outline: none;
      @media #{$tablet} {
        width: 300px;
      }
      @media #{$mobile} {
        width: 100%;
        text-align: center;
        -webkit-appearance: none;
        -webkit-border-radius: 0;
      }
      &::placeholder {
        color: #fff;
        font-weight: 300;
      }
      &.wpcf7-not-valid {
        border-color: red;
        &::placeholder {
          color: red;
        }
      }
    }
    textarea {
      display: block;
      background: none;
      border: none;
      border-bottom: 1px solid #fff;
      color: #fff;
      font-size: 20px;
      font-weight: 300;
      width: 500px;
      padding-bottom: 5px;
      height: 30px;
      margin: 0 auto;
      outline: none;
      @media #{$tablet} {
        width: 300px;
      }
      @media #{$mobile} {
        width: 100%;
        text-align: center;
        -webkit-appearance: none;
        -webkit-border-radius: 0;
      }
      &::placeholder {
        color: #fff;
        font-weight: 300;
      }
    }
    .textarea-292 {
      margin: 0;
    }
    input[type=submit] {
      background: none;
      display: flex;
      justify-content: center;
      align-items: center;
      border: 1px solid #febf00;
      border-radius: 5px;
      color: #febf00;
      font-size: 20px;
      font-weight: 700;
      font-family: $font__headers;
      transition: all 0.3s;
      text-decoration: none;
      width: 350px;
      height: 60px;
      text-transform: uppercase;
      cursor: pointer;
      margin: 0 150px 15px 0;
      padding: 0;
      @media #{$tablet} {
        width: 300px;
        font-size: 18px;
        margin: 0 0 15px 0;
      }
      @media #{$mobile} {
        width: 100%;
        font-size: 18px;
        margin: 20px auto 0;
      }
      &:hover {
        background: #febf00;
        color: #fff;
      }
    }
    div.wpcf7 .ajax-loader, span.wpcf7-not-valid-tip {
      display: none!important;
    }
    .wpcf7-response-output {
      color: #fff;
      text-align: center;
    }
    .mail_link {
      display: flex;
      justify-content: center;
      align-items: center;
      text-decoration: none;
      color: #fff;
      font-size: 18px;
      font-weight: 300;
      margin: 30px auto 0;
      img {
        margin-right: 5px;
      }
    }
  }
}